 <template>
   <div>
    <demo-block :card="true">
      <gw-button size="large" type="default" @click="showOverlay" >show overlay</gw-button>
    </demo-block>
    <gw-overlay
      :mask="mask"
      :show="overlayShow"
      :z-index="overlayIndex"
      @click="onClickOverlay"
    />
   </div>
 </template>
<script>
import { Button, Overlay } from 'gw-vue-ui'

export default {
  components: {
    GwButton: Button,
    GwOverlay: Overlay
  },
  data () {
    return {
      overlayIndex: 1000,
      mask: true,
      overlayShow: false
    }
  },
  methods: {
    showOverlay () {
      this.overlayShow = true
    },
    onClickOverlay () {
      this.overlayShow = false
    }
  }
}
</script>

<style lang="scss" scoped>
.gw-button {
  margin: 0 10px 10px 0;
}
</style>
